:global .slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
:global .slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
:global .slick-list:focus {
  outline: none;
}
:global .slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
:global .slick-slider .slick-track,
:global .slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
:global .slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
:global .slick-track:before, :global .slick-track:after {
  content: "";
  display: table;
}
:global .slick-track:after {
  clear: both;
}
.slick-loading :global .slick-track {
  visibility: hidden;
}
:global .slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}
[dir=rtl] :global .slick-slide {
  float: right;
}
:global .slick-slide img {
  display: block;
}
:global .slick-slide.slick-loading img {
  display: none;
}
:global .slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized :global .slick-slide {
  display: block;
}
.slick-loading :global .slick-slide {
  visibility: hidden;
}
.slick-vertical :global .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
:global .slick-arrow.slick-hidden {
  display: none;
}
.slick-loading :global .slick-list {
  background: #fff url("/ajax-loader.gif") center center no-repeat;
}
:global .slick-prev,
:global .slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}
:global .slick-prev:hover, :global .slick-prev:focus,
:global .slick-next:hover,
:global .slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}
:global .slick-prev:hover:before, :global .slick-prev:focus:before,
:global .slick-next:hover:before,
:global .slick-next:focus:before {
  opacity: 1;
}
:global .slick-prev.slick-disabled:before,
:global .slick-next.slick-disabled:before {
  opacity: 0.25;
}
:global .slick-prev:before,
:global .slick-next:before {
  font-family: "";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:global .slick-prev {
  left: -25px;
}
[dir=rtl] :global .slick-prev {
  left: auto;
  right: -25px;
}
:global .slick-prev:before {
  content: "←";
}
[dir=rtl] :global .slick-prev:before {
  content: "→";
}
:global .slick-next {
  right: -25px;
}
[dir=rtl] :global .slick-next {
  left: -25px;
  right: auto;
}
:global .slick-next:before {
  content: "→";
}
[dir=rtl] :global .slick-next:before {
  content: "←";
}
:global .slick-dotted.slick-slider {
  margin-bottom: 30px;
}
:global .slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}
:global .slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}
:global .slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}
:global .slick-dots li button:hover, :global .slick-dots li button:focus {
  outline: none;
}
:global .slick-dots li button:hover:before, :global .slick-dots li button:focus:before {
  opacity: 1;
}
:global .slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:global .slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}
:global .slick {
  /* &-active {
    opacity: 1;
  } */
}
:global .slick-slide {
  display: block;
  transition: all 0.2s;
}
:global .slick-slide .content-block {
  padding: 2em;
}
:global .slick-list {
  padding-top: 4em !important;
  padding-bottom: 2em !important;
}
:global .slick-track {
  display: flex;
  align-items: center;
}
:global .slick-center {
  transition: transform 0.2s;
  transform: scale(1.2);
  display: block;
}
:global .slick-arrow {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #6bce6f url("/images/icons_48x48.svg");
}
:global .slick-arrow:hover, :global .slick-arrow:focus {
  background: #6bce6f url("/images/icons_48x48.svg");
}
:global .slick-prev {
  left: -48px;
}
:global .slick-next {
  right: -48px;
  transform: scaleX(-1) translateY(-50%);
}
:global .slick-dots button:before {
  transition: all 0.2s;
  border-radius: 50%;
  background: #1b242f;
}

